import { Helmet } from "react-helmet-async";
import { Suspense, useState, useEffect } from "react";
import AdminPanel from "@/spa/screens/AdminPanel";
import AdminLogin from "@/features/admin/AdminLogin";
import ErrorBoundary from "@/features/admin/ErrorBoundary";

export default function AdminPage() {
  const [authed, setAuthed] = useState<boolean | null>(null);

  useEffect(() => {
    // –ü–µ—Ä–µ–≤—ñ—Ä–∫–∞ —á–∏ –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á —É–∂–µ –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω–∏–π –≤ —Ü—ñ–π —Å–µ—Å—ñ—ó
    const isAuthed = sessionStorage.getItem("admin_authed") === "true";
    setAuthed(isAuthed);
  }, []);

  if (authed === null) {
    return <div className="min-h-screen bg-black" />;
  }

  return (
    <ErrorBoundary>
      <Helmet>
        <title>Admin ‚Äî –ì–û ¬´–õ–ï–ì–Ü–û–ù –¢–ò–¢–ê–ù–Ü–í¬ª</title>
        <meta name="robots" content="noindex,nofollow" />
        <link rel="canonical" href="/admin" />
      </Helmet>
      {!authed ? (
        <AdminLogin onAuthed={() => setAuthed(true)} />
      ) : (
        <Suspense fallback={<div className="min-h-screen bg-black flex items-center justify-center">–ó–∞–≤–∞–Ω—Ç–∞–∂—É—î—Ç—å—Å—è...</div>}>
          <AdminPanel />
        </Suspense>
      )}
    </ErrorBoundary>
  );
}

